*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: #bbb;
}

.row{
  width: 320px;
  height: 105px;
  background-color: yellow;
  margin: 5px;
  display: flex;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border-top: 3px solid black;
  color: white;
  align-items: center;
  justify-content: center;

}
div.row > div{
  /* margin: 10px; */
  background-color: blue;
  border-radius: 20px;
  font-size: 50px;
  width: 90px;
  height: 80px;
  margin-left:5px;
  margin: 5px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 2px black;
  
}

.c1{
  color: blue;
}

.App{
  width: 420px;
  margin: 80px auto;
  display: flex;
  justify-content: center;
}
/* 
.ticTak{
  box-shadow:0 2px 5px black
} */

.winner{
  font-size: 25px;
  background-color: black;
  border-radius: 20px;
  color: white;
  text-align: center;
  margin-bottom: 20px;
  padding: 10px;
  width: 340px;
  
}

.btn{
  text-align: center;
}
.btn button{
  font-size: 15px;
  background-color: black;
  border-radius: 15px;
  color: white;
  text-align: center;
  margin-bottom: 20px;
  padding: 10px 40px;
  margin-top: 20px;
  outline: none;
  border: none;
  box-shadow: 0 2px 5px black;
  transition: 0.1s;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}
.btn button:hover{
  transform: translateY(-2px);
}



